<template>
  <div class="app-page evaluate-list">
    <page-content class="page-content">
      <scroller style=""
                :on-refresh="refresh"
                :on-infinite="infinite"
                ref="myscroller">
      <van-tabs v-model="active" color="#22ac38">
        <van-tab title="待评价" >
          <div class="bg_color">
            <div class="top_title">
              <img src="/static/images/personal/pu.png" alt="" >
              <span>普通订单</span>
            </div>
            <div class="order_list" v-for="(item, index) in list" :key="index">
              <img src="/static/images/personal/yz1.png" alt="">
              <div class="mid_title">
                <p >橙子橙子天然维C超甜橙子橙子天然维C超甜橙子橙子天然维C超甜橙子橙子天然维C超甜</p>
                <span>规格：3kg</span>
              </div>
              <div class="right_price">
                <p>￥ 36.00</p>
                <span>x 1</span>
              </div>
              <div class="bottom_combined">
                <p>共 1 件商品 合计 ：￥36.00</p>
                <div class="button_group">
                  <div class="comment_button" @click="goSubmit(0)">
                    评价
                  </div>
                  <div class="check_button" @click="goOrder(0)">
                    查看订单
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="已评价">
          <div class="bg_color">
            <div class="order_list">
              <img src="/static/images/personal/yz1.png" alt="">
              <div class="mid_title">
                <p >橙子橙子天然维C超甜橙子橙子天然维C超甜橙子橙子天然维C超甜橙子橙子天然维C超甜</p>
                <span>规格：3kg</span>
              </div>
              <div class="right_price">
                <p>￥ 36.00</p>
                <span>x 1</span>
              </div>
              <div class="bottom_combined">
                <p>共 1 件商品 合计 ：￥36.00</p>
                <div class="button_group">
                  <div class="check_button"  @click="goDetail(0)">
                    查看评价
                  </div>
                </div>
              </div>
            </div>

          </div>
        </van-tab>
      </van-tabs>
      </scroller>
    </page-content>
  </div>
</template>
<script>
import PageContent from '@/components/page/content'
export default {
  name: 'evaluateList',
  components: { PageContent },
  data () {
    return {
      active: 0,
      list: [1, 1, 1, 1, 1]
    }
  },
  methods: {
    goSubmit () {
      this.$router.push({ path: '/evaluate/submit', query: {} })
    },
    goDetail () {
      this.$router.push({ path: '/evaluate/detail', query: {} })
    },
    goOrder () {
      this.$router.push({ path: '/order/detail', query: {} })
    },
    /** 下拉刷新相关方法****/
    refresh (done) {
      console.log('下拉刷新')
      setTimeout(() => {
        this.list = [1, 1, 1, 1, 1]
        done()
      }, 1000)
    },
    /** 滚动加载相关方法****/
    infinite (done) {
      // 异步更新数据
      setTimeout(() => {
        console.log('滚动触发...')
        if (this.list.length >= 10) {
          done(true)
          this.$refs.myscroller.finishInfinite(true)
          return
        } else {
          this.list = this.list.concat([1, 1])
        }
        done()
      }, 1000)
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .evaluate-list{
    font-family: $font-family-default!important;
    .page-content{
      position: relative;
      background: #f9f9f9;
    }

    .bg_color{

      /*padding-bottom: 40px;*/
      overflow: hidden;
      margin-top: 20px;
      padding: 0 20px;
    }
    .top_title {
      background: #fff;
      font-size: 26px;
      padding: 36px 36px 0 20px;
      img{
        width: 30px;
        height: 30px;
      }
    }
    .order_list{
      background: #fff;
      display: flex;
      flex-direction: row;
      margin-bottom: 20px;
      padding: 40px 20px;
      position: relative;
      height: 320px;
      img{
        width: 150px;
        height: 150px;
      }
      .mid_title {
        width: 320px;
        margin:0 40px;
        p{
          font-size: 28px;
          line-height: 34px;
          margin-bottom: 18px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        span{
          font-size: 24px;
          color: #999;
        }
      }
      .right_price{
        p{
          font-size: 28px;
          margin-bottom: 10px;
        }
        span{
          font-size: 24px;
          color: #999;
          float: right;
        }
      }
    }
    .bottom_combined{
      position: absolute;
      bottom: 40px;
      right: 25px;
      p{
        text-align: right;
      }
      .button_group{
        display: flex;
        flex-direction:row-reverse;
        margin-top: 30px;
        .check_button,.comment_button{
          width: 150px;
          height: 54px;
          color: #666;
          border: 2px solid #999;
          border-radius: 14px;
          font-size: 26px;
          line-height:54px ;
          text-align: center;
        }
        .comment_button{
          color: #22ac38;
          border: 2px solid #22ac38;
          margin-left: 25px;
        }
      }
    }
  }
</style>



